import React, { Fragment, useState, useRef, useEffect } from 'react';
import MailchimpSubscribe from "react-mailchimp-subscribe";
import EmailForm from '../EmailForm';

import { 
	Section,
	BlueCircle,
	YellowTriangle,
	GreenSquare,
	Header,
	Title,
	ButtonJoin,
	ContainerButtonJoin,
	FormJoin,
	InputEmail,
	ButtonAccept,
	ContainerButtonAccept,
	ContainerForm
 } from './styles';


function Join(props) {

	const [ animateButton, setAnimateButton ] = useState(false);
	const [ showEmail, setShowEmail ] = useState(false);
	const [emailError,setEmailError] = useState('');

	function hideButton(){
		setAnimateButton(true);
		setTimeout(() => {
		    setShowEmail(true);
		  }, 300);
		}

	const url = "https://gmail.us17.list-manage.com/subscribe/post?u=297c105be5b21f86f34eb481f&amp;id=39c5b50f26";
	

	return(
		<Section>
			<BlueCircle/>
			<YellowTriangle/>
			<GreenSquare/>
			<Header>
				<Title>Join the Crowd</Title>
			</Header>
			{showEmail ? 
				<React.Fragment> 
					<MailchimpSubscribe url={url} render={props => <EmailForm {...props}/>}/>
				</React.Fragment>	
				: 
				<ContainerButtonJoin animateButton={animateButton}>
					<ButtonJoin onClick={hideButton}>
						Join
					</ButtonJoin>
				</ContainerButtonJoin>	
				}	
		</Section>
	);
}

export default Join;