import React, { useState } from 'react';
import MailchimpSubscribe from "react-mailchimp-subscribe";
import EmailForm from '../../EmailForm';

import { 
	Container,
	ContainerTitle,
	Title,
	Description,
	ButtonJoin,
	ContainerButtonJoin,
 } from './styles';

function HeroTitle(props) {

	const [ animateButton, setAnimateButton ] = useState(false);
	const [ showEmail, setShowEmail ] = useState(false);

	function hideButton(){
		setAnimateButton(true);
		setTimeout(() => {
		    setShowEmail(true);
		}, 300);
	}

	const url = "https://gmail.us17.list-manage.com/subscribe/post?u=297c105be5b21f86f34eb481f&amp;id=39c5b50f26";

	return(
		<Container static={props.static}>
			<ContainerTitle>
				<Title>Find your Crowd</Title>
				<Description>
					You shouldnt feel alone anymore. Connect with new people who share your interests.
				</Description>
			</ContainerTitle>
			{showEmail ?
					<MailchimpSubscribe url={url} render={props => <EmailForm white style={{margin: 0,marginTop: '36px'}}  {...props}/>}/>				
			: 
				<ContainerButtonJoin animateButton={animateButton}>
					<ButtonJoin onClick={hideButton}>
						Join
					</ButtonJoin>
				</ContainerButtonJoin>	
			}
		</Container>		
	);
}

export default HeroTitle;

